<template>
  <div>
    <iframe height="100%" width="100%" :src="getFilePath"></iframe>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PDFJSViewer",
  data() {
    return {
      storeURL: String,
    };
  },
  props: {
    fileName: String,
    path: String,
  },
  computed: {
    getFilePath() {
      // download pdf to blob
      this.downloadFile();

      if (this.fileName !== "") {
        return this.path + "?file=" + this.fileName;
      }
      return this.path;
    },
  },
  methods: {
    downloadFile() {
      axios({
        url: "https://pdftron.s3.amazonaws.com/downloads/pl/demo-annotated.pdf", // download file link goes here
        method: "GET",
        responseType: "blob",
        headers: this.headers,
      }).then((res) => {
        var FILE = URL.createObjectURL(new Blob([res.data]));
        this.storeURL = FILE;
        console.log(FILE + "FIELEE");
        // console.log("RES" + res.headers);
        // console.log("HERE" + FILE);
      });
    },
    // blobTOPDF() {
    //   const getFileLoad = (event) =>
    //     console.log("FileRead.onload ", event.target.result);
    //   const fileReader = new FileReader();
    //   // 成功load資料後觸發
    //   fileReader.addEventListener("load", getFileLoad);
    //   // 讀取並轉成ArrayBuffer
    //   fileReader.readAsArrayBuffer(blob);
    // },
  },
};
</script>

<style scoped>
div {
  width: 100%;
  height: 100vh;
  min-width: 400px;
}
</style>
